table {
  width: 100%;
  font-size: inherit;
}

table thead {
  background-color: $color-tbl-th;
  line-height: 1em;
}

table tbody tr:nth-child(odd) { background-color: $color-tbl-tr; }

table th { padding: rem-calc($padding-main $padding-main); }
table td { padding: rem-calc($padding-main/2 $padding-main/2); }
table th, table td {
  // font-size: 0.9em;
  font-weight: normal;
  text-align: center;
}

table [class*="icon"] {
  font-size: 1.5rem;
  color: $color-asphalt;
}

// сортировка по времени
table .icon-sort {
  margin-right: rem-calc($padding-main)/2;
  font-size: 1em;
  color: $color-asphalt;
  line-height: inherite;
}

// отобразим по вертикали
table .asc .icon-sort {
  -moz-transform: rotate(90deg);
  transform: rotate(90deg);
}

.card_active .icon-card {
  @extend .icon-card;
}

table .icon-card-block,
table .card_blocked .icon-card {
  @extend .icon-card-block;
  position: relative;
  &:after {
    font-family: "icons";
    position: absolute;
    content: 'o';
    top: 0; right: 0; bottom: 0; left: 0;
    color: $color-red;
  }
}

table .icon-debit {
  &:before {
    @extend .icon-card-in;
    vertical-align: middle;
    color: $color-red;
  }
}

table .icon-credit {
  &:before {
    @extend .icon-card-out;
    vertical-align: middle;
    color: $color-green;
  }
}

table .icon-doc-create,
table .icon-doc-complete,
table .icon-doc-ignored {
  vertical-align: middle;
  line-height: inherit;
  color: $color-red;
  font-weight: 900;
  font-size: inherit;
  &:before {
    line-height: inherit;
    font-size: 2em;
    vertical-align: middle;
    color: $color-green;
    padding-right: rem-calc($padding-main);
  }
}

table .icon-doc-create:before   { color: $color-asphalt; }
table .icon-doc-ignored:before  { color: $color-red;     }
table .icon-doc-complete:before { color: $color-green;   }

table a:hover .icon-doc-create:before,
table a:hover .icon-doc-complete:before {
  color: $color-red;
}

table .icon-doc-sended {
  @extend .icon-doc-create;
}

tfoot {
  background-color: lighten($color-asphalt, 60%);
}



.pagination {
  ul, li, a {
    display: inline-block;
    padding: 0;
    margin: 0;
  }
}

.pagination li a{
  padding: rem-calc($padding-main/2);
  margin-left: rem-calc(0 $padding-main/2);
  min-width: 2rem;
}
.pagination li.current a {
  background-color: $color-asphalt;
  color: white;
};

.pagination li a:hover {
  background-color: lighten($color-asphalt, 50%);
}


/*******************************************
         стилим виджет таблиц yii
============================================*/

.button-column { width: rem-calc(65px);}
.summary, .pager {
  line-height: 2.5rem;
  padding: rem-calc(0 $padding-main);
  text-align: right;
}

.pager {
  @extend tfoot;
  margin-bottom: rem-calc($padding-main*2);
}

.page, .previous, .next {
  font-size: 1rem;
}

// спрячем иконки
a.view, a.update, a.delete {
  font-family: icons;
  padding: rem-calc(0 $padding-main/6);
  &:hover { color: $color-red; }
  & img { @extend .hidden; }
}
// заменим содержимым
a.view   { @extend .icon-view; }
a.update { @extend .icon-update; }
a.delete { @extend .icon-delete; }

ul.yiiPager .selected a:visited {
  background: $color-asphalt none repeat scroll 0% 0%;
  border: 0px;
  color: white;
}

ul.yiiPager a:hover:link,
ul.yiiPager a:hover:visited {
  background-color: lighten($color-asphalt,50%);
}

ul.yiiPager a:link,
ul.yiiPager a:hover:link,
ul.yiiPager a:visited,
ul.yiiPager a:hover:visited {
  color: $color-asphalt;
  border: 0px;
}

ul.yiiPager li.hidden {
  @extend .hidden;
}
