

html { height: 100%; }
body { min-height: 100%;
}

input[type=checkbox] {
  margin: 0;
  margin-right: rem-calc($padding-main)/2;
  font-size: inherit;
  vertical-align: middle;
  line-height: inherit;
}

textarea,
input[type='text'],
input[type='date'],
input[type='password'],
input[type='number'],
input[type='email'],
select {
  margin: 0;
  display: inline-box;
  padding: rem-calc($padding-main)/4;
  height: auto;
  box-shadow: none;
}

body > .content {
  // высота хедера с отступом + высота футера
  $calc: rem-calc($header-height + 8*$padding-main + $footer-height);
  min-height: calc(100vh - #{$calc});
}

// второстемпенное меню на главной странице
@media only screen and (max-width: 40.063em) {
  .content { min-height: auto; }
}


// красаня полоса сверху сайта
.line {
  margin-bottom: rem-calc(4*$padding-main);
  border-top: rem-calc($padding-main)/2 solid $color-red;
}

// фоновая картинка при заходе на сайт
.bg-login {
  // background-image: url('../images/bg-login-pattern.gif'), url('../images/bg-login.jpg');
  // background-size: auto auto, cover;
  &:before, &:after {
    content: '';
    position: fixed;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: -1;
  }
  &:before {
    background-image: url('../images/bg-login.jpg');
    background-repeat: no-repeat;
    background-size: cover;
  }
  &:after {
    background-image: url('../images/bg-login-pattern.gif');
    opacity: .1;
  }
}

.bg-gray {
  background-color: $color-bg;
}

.flexbox-container {
  padding: rem-calc(10px) 0;
  display: flex;
  align-items: center;
  justify-content: center;
  -ms-flex-pack: justify;
  -ms-flex-align: center;
}


.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}

a {
  text-decoration: none;
  color: inherit;
}

a.dashed {
  border-bottom: {
  width: 1px;
  style: dashed;
  color: inherit;
  }
  &:hover {
    border-bottom: none;
  }
}

#map {
  width: 100%;
  height: rem-calc($height-contact-map);
}

.overflow {
  overflow: hidden;
}

.hidden {
  display: none;
  visibility: hidden;
}

p.error {
  color: $color-red;
  font-weight: bold;
  background-color: lighten($color-orange, 40%);
  border: 1px solid $color-orange;
  line-height: 2.5rem;
  text-align: center;
}

form {
  margin: 0;
}
