
.modal-container {
  @extend .flexbox-container;
  display: none;
  position: fixed;
  top: 0; bottom: 0; left: 0; right: 0;
  z-index: 9999;
  background-color: rgba(0,0,0,.8);

  a[href='#close'] {
    font-size: 2rem;
    line-height: 1.5rem;
    float: right;
    padding-left: rem-calc($padding-main);
    padding-right: rem-calc($padding-main);
    margin-right: rem-calc(-4*$padding-main);
    &:hover { color: black; }
  }

}

textarea { margin: 0; }

.modal header {
  overflow-y: auto;
  font-size: 1.1rem;
  font-weight: 600;
}