@charset "UTF-8";

@font-face {
  $path: '../fonts/icons/';
  font-family: "icons";
  src:url($path + "abns.eot");
  src:url($path + "abns.eot?#iefix") format("embedded-opentype"),
      url($path + "abns.woff") format("woff"),
      url($path + "abns.ttf") format("truetype"),
      url($path + "abns.svg#abns") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]:before {
  font-family: "icons" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "icons" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


