body > header {

  font-size: rem-calc(12px);
  span.logo {
    display: inline-block;
  }
  img.logo { margin: 0;  }
  .logo {
    margin-top: 2*rem-calc($padding-main);
    // height: rem-calc($logo-height);
    font: {
      size: rem-calc(14px);
      weight: 900;
    }
    // &:before {
    //   content: '';
    //   display: block;
    //   height: rem-calc($logo-height);
    //   background-image: url('../images/logo_nnk_aliance.svg');
    //   background-repeat: no-repeat;
    //   background-position: 0 0;
    //   background-size: auto 100%;
    // }
  }

  a {
    margin-left: 2*rem-calc($padding-main);
    &:hover {
      color: $color-red;
    }
  }

  [class*='icon'] {
    &:before {
      line-height: inherit;
      vertical-align: middle;
      padding-right: rem-calc($padding-main)/2;
      border: none;
      color: red;
    }
  }
}



@media only screen and (max-width: 40.063em) {
  body > header { margin-bottom: 2*rem-calc($padding-main); }
}