
#login {
  position: static;
}

.modal {
  // отступы текста от границы формы
  $padding-form: 4*rem-calc($padding-main);

  background-color: $color-tbl-tr;
  box-shadow: 0 0 rem-calc($padding-main)/2 rgba(0,0,0,0.5);
  font: {
    size: rem-calc(12px);
  }

  // отступы текста от границы формы
  .container {
    padding: {
      left: $padding-form;
      right: $padding-form;
    }
  }

  footer.container,
  section.container {
    margin-top: $padding-form;
  }

  footer { padding-bottom: $padding-form; }

  header {
    margin: 0;
    padding: rem-calc($padding-main);
    color: white;
    background-color: $color-red;
    font-size: 1.5em;
  }

  p { margin-top: 0; }

  .btn {
    width: 100%;
    margin-bottom: 5px;
  }
}

.btn {
  margin: 0 1px;
  padding: 0;
  display: inline-block;
  line-height: 2.5rem;
  color: white;
  border: 1px solid $color-red;
  text: {
    decoration: none;
    align: center;
  }
  font: {
    weight: bold;
    size: 1.2em;
  }
  background: $color-gradient-orange;
  &:hover {
    background: $color-gradient-red;
  }
}

.error_msg {
  text-align: center;
  line-height: 2.5rem;
  background-color: $color-red;
  font-weight: bold;
  font-size: 1.2em;
  color: white;
}