
.cards {
  margin-top: rem-calc($padding-main);
  background-color: white;
  border: 1px solid $color-tbl-th;
  font-size: rem-calc(12px);

  header {
    background: $color-gradient-grey;
  }

  .container {
    margin: {
      left: 2*rem-calc($padding-main);
      right: 2*rem-calc($padding-main);
    }
  }

  .title {
    border-bottom: 1px solid $color-tbl-th;
  }

}

.title {
  h3 {
    margin: 0;
    font:{
      size: 1.2rem;
      weight: normal;
    }
    color: $color-red;
  }

  p {
    margin: 0;
    text-align: right;
    color: $color-asphalt;
  }

  .time {
    padding: rem-calc($padding-main)/2;
    background-color: $color-tbl-tr;
    font-weight: bold;
  }

  // ширина блока .title
  h3, p { line-height: 3rem; }
}

.subtitle {
  label, input[type=checkbox], span {
    padding: 0;
    display: inline-block;
    font-size: inherit;
    // высота блока
    line-height: 2rem;
    vertical-align: middle;
  }
  input[type=checkbox] {
    margin-left: rem-calc($padding-main);
  }
}

.usermenu_groups {
  margin: 0;
  margin-top: rem-calc($padding-main);
  padding: 0;
  white-space: 0;
  list-style-type: none;
  font-weight: bold;
  color: $color-asphalt;
  border-bottom: rem-calc($padding-main)/2 solid $color-orange;

  & li {
    padding-left: 2*rem-calc($padding-main);
    padding-right: 2*rem-calc($padding-main);
    // избавляемся от пробела
    margin-right: rem-calc(-4px);
    display: inline-block;
    border-radius: rem-calc($padding-main/2 $padding-main/2 0 0);
    line-height: 4*rem-calc($padding-main);
    height: 4*rem-calc($padding-main);
  }

  & li:hover { background-color: $color-orange; }

  & .active, & .active:hover {
    background-color: $color-red;
    color: white;
  }

  & a {
    display: block;
    // background-color: red;
    width: 100%;
    color: inherit;
    text-decoration: none;
  }
}

.container.subtitle {
  margin-top: rem-calc($padding-main);
  margin-bottom: rem-calc($padding-main);
}

.cards input[type='date'],
.cards input[type='text'],
.cards input[type='number'] {
  // font-size: 0.9em;
  line-height: inherit;
  // display: inline-block;
  // width: rem-calc(160px);
}

.bg-login form input[type='text'] {
  margin-bottom: rem-calc($padding-main);
}

.cards input.datepicker,
.cards input.text-right {
  margin-left: rem-calc($padding-main)/4;
  width: rem-calc(160px);
  display: inline-block;
}

.cards .btn {
  // width: 99%;
  padding: rem-calc(0 $padding-main);
  line-height: inherit;
  &:before {
    vertical-align: middle;
    line-height: inherit;
    padding-right: rem-calc($padding-main);
  }
}

.footer {
  line-height: 2rem;
  font-size: 1.2em;
  font-weight: bold;
  background-color: $color-tbl-th;
  color: $color-asphalt;
  span {
    vertical-align: middle;
    margin: 0 rem-calc($padding-main);
  }
  span:before {
    font-size: 1.3em;
    vertical-align: middle;
  }
}

.cards-all, .cards-details, .cards-total, .reserve-btn, .reserve-table {
  padding: rem-calc(2*$padding-main 0);
}

.cards-total {
  .footer {
    background-color: darken($color-tbl-th, 10%);
  }
}


// кнопка резерва
.reserve-btn  .btn {
  line-height: inherit;
  padding: rem-calc(1.5*$padding-main 6*$padding-main);
}

.timer {
  padding: rem-calc(0 2*$padding-main);
  display: inline-block;
  background-color: $color-orange;
  color: white;
  font-weight: bold;
  line-height: 2.5rem;

}

#time {
  padding-left: rem-calc(2*$padding-main);
  font-size: 1.3em;
}

.reserve-timer {
  margin-top: rem-calc($padding-main);
}